import enTranslations from './en.json';
import zhTwTranslations from './zh-tw.json';
import zhCnTranslations from './zh-cn.json';
import jaTranslations from './ja.json';
import koTranslations from './ko.json';
// 
import deTranslations from './de.json';
import esTranslations from './es.json';
import frTranslations from './fr.json';
import itTranslations from './it.json';
import ptTranslations from './pt.json';
import nlTranslations from './nl.json';
import svTranslations from './sv.json';
import ruTranslations from './ru.json';

const translations = {
  'en': enTranslations,
  'zh-tw': zhTwTranslations,
  'zh-cn': zhCnTranslations,
  'ja': jaTranslations,
  'ko': koTranslations,
  // 
  'de': deTranslations,
  'es': esTranslations,
  'fr': frTranslations,
  'it': itTranslations,
  'pt': ptTranslations,
  'nl': nlTranslations,
  'sv': svTranslations,
  'ru': ruTranslations
};

class I18n {
  constructor() {
    this.currentLanguage = this.loadLanguage();
    this.listeners = [];
  }

  loadLanguage() {
    //  localStorage
    const saved = localStorage.getItem('diablo-language');
    if (saved && translations[saved]) {
      return saved;
    }
    
    // 
    const browserLang = navigator.language || navigator.userLanguage || 'en';
    
    // 
    if (browserLang.startsWith('zh')) {
      if (browserLang.includes('TW') || browserLang.includes('HK') || browserLang.includes('MO')) {
        return 'zh-tw';
      } else if (browserLang.includes('CN') || browserLang.includes('SG')) {
        return 'zh-cn';
      }
      // 
      return 'zh-cn';
    } 
    // 
    else if (browserLang.startsWith('ja')) {
      return 'ja';
    } else if (browserLang.startsWith('ko')) {
      return 'ko';
    }
    // 
    else if (browserLang.startsWith('de')) {
      return 'de';
    } else if (browserLang.startsWith('es')) {
      return 'es';
    } else if (browserLang.startsWith('fr')) {
      return 'fr';
    } else if (browserLang.startsWith('it')) {
      return 'it';
    } else if (browserLang.startsWith('pt')) {
      return 'pt';
    } else if (browserLang.startsWith('nl')) {
      return 'nl';
    } else if (browserLang.startsWith('sv')) {
      return 'sv';
    } else if (browserLang.startsWith('ru')) {
      return 'ru';
    }
    
    return 'en';
  }

  setLanguage(lang) {
    if (translations[lang] && this.currentLanguage !== lang) {
      this.currentLanguage = lang;
      localStorage.setItem('diablo-language', lang);
      this.notifyListeners();
    }
  }

  getCurrentLanguage() {
    return this.currentLanguage;
  }

  getAvailableLanguages() {
    return [
      { code: 'en', name: 'English' },
      { code: 'zh-tw', name: '' },
      { code: 'zh-cn', name: '' },
      { code: 'ja', name: '' },
      { code: 'ko', name: '' },
      // 
      { code: 'de', name: 'Deutsch' },
      { code: 'es', name: 'Espaol' },
      { code: 'fr', name: 'Franais' },
      { code: 'it', name: 'Italiano' },
      { code: 'pt', name: 'Portugus' },
      { code: 'nl', name: 'Nederlands' },
      { code: 'sv', name: 'Svenska' },
      { code: 'ru', name: '' }
    ];
  }

  t(key, params = {}) {
    const keys = key.split('.');
    let value = translations[this.currentLanguage];
    
    for (const k of keys) {
      if (value && typeof value === 'object' && k in value) {
        value = value[k];
      } else {
        // Fallback to English if key not found
        value = translations.en;
        for (const k2 of keys) {
          if (value && typeof value === 'object' && k2 in value) {
            value = value[k2];
          } else {
            return key; // Return key if not found in any language
          }
        }
        break;
      }
    }
    
    if (typeof value === 'string') {
      // Replace parameters in the string
      return value.replace(/\{(\w+)\}/g, (match, paramName) => {
        return params[paramName] || match;
      });
    }
    
    return key;
  }

  addListener(callback) {
    this.listeners.push(callback);
  }

  removeListener(callback) {
    this.listeners = this.listeners.filter(listener => listener !== callback);
  }

  notifyListeners() {
    this.listeners.forEach(callback => callback(this.currentLanguage));
  }
}

const i18n = new I18n();

export default i18n;
